.about-content {
  border-left: 1px solid #698878d5;
  border-right: 1px solid #698878d5;
  border-top: 1px solid #698878d5;
  border-bottom: 1px solid #698878d5;
  background: white;
}

.about-content header {
  font-size: 16pt;
  margin-left: 16px;
  margin-right: 16px;
  margin-top: 40px;
}

@media only screen and (max-width: 600px) {
  .about-content img {
    width: 170px;
    height: 220px;
    background-image: url("../../../../resources/images/grad_pic_2.jpg");
    background-size: 100%, 100%;
    border: 1px solid gray;
    margin-left: 16px;
    margin-right: 16px;
  }
}

